.container {
  .top_container {
    display: flex;
    width: 100%;
    justify-content: center;
  }

  .bottom_container {
    display: flex;
    width: 100%;
    justify-content: space-around;
  }

  .img {
  }

  .intro {
    display: flex;
    align-items: center;
    font-size: 26px;
    font-weight: 600;
    text-align: center;

    span {
      display: inline-block;
      box-shadow: 0px 2px 2px #506bf2;
    }
  }

  .skills_card,
  .contact_card {
    border-radius: 25px;
    background: #ffffff;
    box-shadow: 0 0 10px #9e9fa5;
    width: 45%;
    padding-bottom: 10px;
    min-height: 150px;

    .title {
      display: flex;
      justify-content: center;
      align-items: center;
    }

    .skills_list {
      display: flex;
      list-style: none;
      justify-content: start;
      flex-wrap: wrap;
      margin-left: 24px;

      li {
        width: 100px;
        margin-bottom: 8px;
      }
    }

    .contact_list {
      list-style: none;

      li {
        margin-bottom: 8px;
        display: flex;
        align-items: center;

        a {
          text-decoration: none;
          margin-left: 5px;
          color: #506bf2;
        }
      }
    }
  }
}
